@-webkit-keyframes loading {
  from {background-color: white;}
  to {opacity:0}
}


@keyframes loading {
  from {background-color: white;}
  to {opacity:0}
}

@-webkit-keyframes afterpreloadtransition
{
  from {opacity:0}
  to {opacity :1}
}

@keyframes afterpreloadtransition
{
  from {opacity:0}
  to {opacity :1}
}

.afterpreload {
  -webkit-animation:afterpreloadtransition;
          animation:afterpreloadtransition;
  -webkit-animation-duration:1.5s;
          animation-duration:1.5s;
