          align-items: center;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  position: relative;
  width: 475px;
  height: 475px;
}

.picturediv {
  margin: 4px;
  padding:0px;
  width: 475px;
  height: 475px;
}

.picturediv:hover .section {
  cursor: pointer;
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  transition: 1s;
}

.picturediv:hover .picturetext {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
  width: 475px;
  height: 340px;
   transition: opacity .2s, visibility .2s;
}

.picturetext {
  -webkit-transform: translate(-0%, -100%);
          transform: translate(-0%, -100%);
  color: #fff;
  visibility: hidden;
  opacity: 0;
  text-align: center; 
  color: white;
  font-size: 3rem;
  position: absolute;
}

